 scores, class, and level determined you can move to picking your spells. The system makes it's best guess using what it knows about how many spells you can prepare (if a preparing class) or have known (if a spells known class) at the top of the spells section. You can do a full text search, filter by spell level, and filter by class spell lists. When you select a spell be sure to fill in the 'Spell Added By Class' dropdown (otherwise it will not properly count against your number prepared/known). Then, in the spell list itself, hit the check mark next to each spell to prepare it or properly mark it as known. To browse through your spells you can select a spell level at the top of the spell section. Clicking on a spell will open its full spell description in a modal.\n          ")])]),_c('li',[_c('strong',[_vm._v("Picking Equipment")]),_c('p',[_vm._v("\n            In the equipment section things are managed as items within containers. If you select an armor item it will be automatically added to the \"Armor\" section as well. To then equip that armor select the checkbox.\n          ")])]),_c('li',[_c('strong',[_vm._v("Armor")]),_c('p',[_vm._v("If you added your armor in the equipment section it will appear here automatically. However you can also add things like magical effect based armor modifiers here. Three examples below:")]),_c('ul',[_c('li',[_vm._v("Mage Armor: The mage armor spell acts like Light Armor (uses your full dex bonus, is a AC formula), so select Light Armor from the \"Type\" dropdown. In the AC enter the base AC exactly as it appears in the description of the spell. In the case of Mage Armor this would be 13. Enter Mage Armor in the name field, and then hit OK. You should see it in the Armor list now. The AC listed in the list may not match what you entered, this is because it is automatically adding any appropriate modifiers from your dexterity.")]),_c('li',[_vm._v("Shield Spell: The shield spell acts like a physical shield, granting a static, stacking AC bonus. Therefore select \"Shield\" from the Type dropdown. Then enter the AC bonus exactly as it appears in the spell description, in this case 5.")]),_c('li',[_vm._v("Monk Unarmored Defense: A monks unarmored defense is an 'unarmored bonus'. A new formula for AC that involves an ability score other than just dexterity. To calculate this select the 'Unarmored Bonus' option from the 'Type' dropdown. A new field will appear, 'Unarmored Bonus Stat'. Because a monks unarmored defense uses wisdom select that from the dropdown. In this case the 'AC' field would be used for any additional static bonus, in this case you can leave it at 0.")])])]),_c('li',[_c('strong',[_vm._v("Skills and Proficiencies")]),_c('p',[_vm._v("\n            The Character builder can automatically calculate skill bonuses if you select the appropriate proficiency level from the dropdown for each skill. For each skill there are 4 options, 'No', 'Yes', 'Half', and 'Exp'. If you are not proficient in a skill, leave it at 'No'. If you have a normal proficiency in a skill, select 'Yes'. The other 2 are special cases. If you have an ability that allows you to add double your proficiency bonus to a skill, such as Expertise, select 'Exp'. If you have something that allows you to add half your proficiency bonus to a skill, such as a bards 'Jack of all Trades' feature, select 'Half'. For things like tool proficiencies, languages, etc enter them in the Proficiencies and Languages box below the standard abilities.\n          ")])])])])])],1)}
